        margin-left: 0;
        padding: 0;
        border-left: 1px solid #85C998;
        border-radius: 0 3px 3px 0
    }

    .layim-send-set .layui-edge {
        top: 14px;
        left: 9px;
        border-width: 6px;
        border-top-style: solid;
        border-top-color: #fff
    }

    .layim-chat-send .layim-menu-box {
        left: auto;
        right: 0;
        top: 33px;
        width: 180px;
        padding: 10px 0
    }

    .layim-chat-send .layim-menu-box li {
        padding-right: 15px;
        line-height: 28px
    }

    body .layui-layim-min {
        border: 1px solid #D9D9D9
    }

    .layui-layim-min .layui-layer-content {
        margin: 0 5px;
        padding: 5px 10px;
        white-space: nowrap
    }

    .layui-layim-close .layui-layer-content span {
        width: auto;
        max-width: 120px
    }

    body .layui-layim-members {
        margin: 25px 0 0 -75px;
        border: none;
        background: 0 0
    }

    body .layui-layim-members .layui-layer-content {
        padding: 0;
        background: 0 0;
        color: #666;
        box-shadow: none
    }

    .layui-layim-members .layui-layer-TipsG {
        display: none
    }

    .layui-layim-members ul {
        position: relative;
        width: 578px;
        height: 200px;
        padding: 10px 10px 0;
        border: 1px solid #D9D9D9;
        background-color: #fff;
        background-color: rgba(255, 255, 255, .9);
        box-shadow: none;
        overflow: hidden;
        font-size: 0
    }

    .layui-layim-members ul:hover {
        overflow: auto
    }

    .layim-add-img,
    .layim-add-remark,
    .layui-layim-members li {
        display: inline-block;
        *display: inline;
        *zoom: 1;
        vertical-align: top;
        font-size: 14px
    }

    .layui-layim-members li {
        width: 112px;
        margin: 10px 0;
        text-align: center
    }

    .layui-layim-members li a {
        position: relative;
        display: inline-block;
        max-width: 100%
    }

    .layui-layim-members li a:after {
        content: '';
        position: absolute;
        width: 46px;
        height: 46px;
        left: 50%;
        margin-left: -23px;
        top: 0;
        border: 1px solid #eee;
        border-color: rgba(0, 0, 0, .1);
        border-radius: 100%
    }

    .layui-layim-members li img {
        width: 48px;
        height: 48px;
        border-radius: 100%
    }

    .layui-layim-members li:hover {
        opacity: .9
    }

    .layui-layim-members li a cite {
        display: block;
        padding: 0 3px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap
    }

    body .layui-layim-contextmenu {
        margin: 70px 0 0 30px;
        width: 200px;
        padding: 5px 0;
        border: 1px solid #ccc;
        background: #fff;
        border-radius: 0;
        box-shadow: 0 0 5px rgba(0, 0, 0, .2)
    }

    body .layui-layim-contextmenu .layui-layer-content {
        padding: 0;
        background-color: #fff;
        color: #333;
        font-size: 14px;
        box-shadow: none
    }

    .layui-layim-contextmenu .layui-layer-TipsG {
        display: none
    }

    .layui-layim-contextmenu li {
        padding: 0 15px 0 35px;
        cursor: pointer;
        line-height: 30px
    }

    .layui-layim-contextmenu li:hover {
        background-color: #F2F2F2
    }

    .layim-add-box {
        margin: 15px;
        font-size: 0
    }

    .layim-add-img img,
    .layim-add-remark p {
        margin-bottom: 10px
    }

    .layim-add-img {
        width: 100px;
        margin-right: 20px;
        text-align: center
    }

    .layim-add-img img {
        width: 100px;
        height: 100px
    }

    .layim-add-remark {
        width: 280px
    }

    .layim-add-remark .layui-select {
        width: 100%;
        margin-bottom: 10px
    }

    .layim-add-remark .layui-textarea {
        height: 80px;
        min-height: 80px;
        resize: none
    }

    .layim-tab-content,
    .layui-layim-face ul,
    .layui-layim-tab {
        margin-bottom: 0
    }

    .layim-tab-content li h5 {
        margin-top: 0;
        margin-bottom: 0
    }

    ,
    .layui-layim-face img {
        vertical-align: bottom
    }

    .layim-chat-other span {
        color: #444
    }

    .layim-chat-other span cite {
        padding: 0 15px;
        color: #999
    }

    .layim-chat-other:hover {
        text-decoration: none
    }

</style>
<template>
  <div class="layout">

      <Row type="flex">
          <i-col span="5" class="layout-menu-left">
              <Menu :active-name="$route.name" router theme="dark" width="auto" :open-names="['0']">
                  <div class="layout-logo-left"></div>
                  <Submenu :name="index+ ''"  v-for="(item,index) in $router.options.routes">
                      <template slot="title">
                          <Icon type="ios-navigate"></Icon>
                          {{item.name}}
</template>
                      <router-link active-class="ivu-menu-item-active ivu-menu-item-selected" :name="child.name" tag="li" v-for="child in item.children"    :to="child.path" class="ivu-menu-item" >{{child.name}}</router-link>
                  </Submenu>

              </Menu>
          </i-col>
          <i-col span="19" class="realtive">
            <Row type="flex" justify="end" class="code-row-bg layout-header" align="middle">
    <Col span="4"> <span style="padding:5px">{{CHAT.username}}</span><a href="http://yibu.a10store.com">进入前台</a></Col>
</Row>
              <div class="layout-breadcrumb">
                  <Breadcrumb>
                      <Breadcrumb-item href="/">首页</Breadcrumb-item>
                      <Breadcrumb-item>{{$route.name}}</Breadcrumb-item>
                  </Breadcrumb>
              </div>
              <div class="layout-content">
                  <div class="layout-content-main">
{{CHAT.msgArr}}
                               <transition name="fade" mode="out-in">
                                 <router-view></router-view>
                               </transition>
                    </div>
              </div>
            
              <div class="layout-copy">

              </div>
          </i-col>
      </Row>
      <div class="btn-online"><Button @click="modal5 = true">在线 {{CHAT.onlineCount}}</Button></div>
      <Modal
        v-model="modal5"
        title="自定义宽度"
        width="300"
        :styles="{right: '0px',position:'fixed',bottom:'0',top:'auto'}" class="layui-layer layui-layer-page layui-box layui-layim layer-anim-02">
        <div slot="header"   style="cursor: move;">  <div class="layui-layim-info">
               <div class="layui-layim-user">{{CHAT.username}}</div>
               <div class="layui-layim-status">
                 <span class="layui-icon layim-status-online" layim-event="status" lay-type="show"></span>
                 <ul class="layui-anim layim-menu-box" style="display: none;">
                   <li class="layim-this" layim-event="status" lay-type="online"><i class="layui-icon"></i>
                     <cite class="layui-icon layim-status-online"></cite>在线</li>
                     <li layim-event="status" lay-type="hide"><i class="layui-icon"></i><cite class="layui-icon layim-status-hide"></cite>隐身</li>
                   </ul></div>
                   <input class="layui-layim-remark" placeholder="编辑签名" value="在深邃的编码世界，做一枚轻盈的纸飞机"></div></div>
        <div class="layui-layer-content">
          <div class="layui-layim-main">
<ul class="layui-unselect layim-tab-content layui-show layim-list-friend">
  <ul class="layui-layim-list layui-show" >
  <li layim-event="chat"  class="layim-friend100001" v-for="cm in CHAT.onlineUsers" >
    <img src="//tva1.sinaimg.cn/crop.0.0.118.118.180/5db11ff4gw1e77d3nqrv8j203b03cweg.jpg">
  <span>{{cm.username}}</span><p>这些都是测试数据，实际使用请严格按照该格式返回</p><span class="layim-msg-status">new<span></span></span></li>
</ul>
</ul>
</div>
        </div>
<div slot="footer">

<ul class="layui-unselect layui-layim-tool"><li class="layui-icon layim-tool-search" layim-event="search" title="搜索"></li><li class="layui-icon layim-tool-msgbox" layim-event="msgbox" title="消息盒子"><span class="layui-anim"></span></li><li class="layui-icon layim-tool-find" layim-event="find" title="查找"></li><li class="layui-icon layim-tool-skin" layim-event="skin" title="更换背景"></li><li class="layui-icon layim-tool-about" layim-event="about" title="关于"></li></ul>
</div>
    </Modal>
  </div>
</template>
<script>
import CHAT from '../api/client'

export default {
    methods: {
        handleSelect(key, keyPath) {
            console.log(this);
        }
    },
    data() {
        return {
            // note: changing this line won't causes changes
            // with hot-reload because the reloaded component
            // preserves its current state and we are modifying
            // its initial state.
            msg: '',

            CHAT: CHAT,
            modal5: false
        }
    },
    mounted() {
        // this.username = this.$cookie.get('username');
        CHAT.init()
        console.log(this)
    }
}
</script>



